<template>
  <div v-if="botStore.botCount > 0">
    <h3 v-if="!small">Available bots</h3>
    <b-list-group>
      <b-list-group-item v-for="bot in botStore.availableBots" :key="bot.botId"
        :active="bot.botId === botStore.selectedBot" button :title="`${bot.botId} - ${bot.botName} - ${bot.botUrl} - ${botStore.botStores[bot.botId].isBotLoggedIn ? '' : 'Login info expired!'
          }`" @click="botStore.selectBot(bot.botId)">
        <bot-rename v-if="editingBots.includes(bot.botId)" :bot="bot" @saved="stopEditBot(bot.botId)"
          @cancelled="stopEditBot(bot.botId)" />

        <bot-entry v-else :bot="bot" :no-buttons="small" @edit="editBot(bot.botId)"
          @editLogin="editBotLogin(bot.botId)" />
      </b-list-group-item>
    </b-list-group>
    <LoginModal v-if="!small" ref="loginModal" class="mt-2" login-text="Add new bot" />
    <br>
